* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "PostGrotesk Bold", sans-serif;
  font-size: $m-size;
}

button {
  cursor: pointer;
}

button:disabled {
  cursor: default;
}

.is-active {
  font-weight: bold;
}

a {
  text-decoration: none;
  color: $blue;
}



